    box-sizing: border-box;
}

body{
 background-color: #fff ;
 margin: 0;
 
}
header{
    background-color: #4F67FF;
    padding: 2rem 8rem;
   
}


button{
    display: flex;
    background-color: #00CDC4;
    font-family: "Gotham Rounded", Sans-serif;
    font-size: 16px;
    letter-spacing: -0.4px;
    justify-content: center;
    color: #fff;
    padding: 1rem;
    border: none;
    border-radius: 5px;
    width: 60%;
}

.hero{
    display: flex;
    flex-direction: row;
}
.hero-left{
    display: flex;
    flex-direction: column;
    /*min-width: 300px;*/
    flex: 1;
}
.hero-right{
    flex: 1;
    max-width: 100vw;
    display: flex;
    align-items: end;
    justify-content: end;
    /*position: relative;*/
    height: 100%;
}

.hero p{
    font-size: 16px;
    color: #fff;
}
.hero-text{
font-family: "Neuton";
color: #00CDC4;
font-size: 56px;

}



/* section */

section{
    padding: 2rem 8rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    font-family: "Neuton";

}

section .right {
    display: flex;
    flex-direction: column;
}
section h2, .certificates h2{
    margin: 0;
    font-size: 32px;
    font-weight: 700;
    line-height: 1.13em;
    letter-spacing: -0.4px;
}
section h3{
    margin: 2rem 0;
    font-size: 28px;
    font-weight: 400;
}
section span{
    color: gray;
    font-size: 18px;
    display: flex;
    gap: 1rem;
    align-items: center;
    margin-bottom: 1rem;
}
section span svg{
    fill: #00CDC4;
}
/**/ 
.img-group{
    display: flex;
    justify-content: space-between;
    margin-top: 1.5rem;
}

.span-group{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.span-group span {
    display: flex;
    align-items: center;
    gap: .5rem;
    margin-bottom: 1rem;
}
.span-group svg{
    fill: #00CDC4;
}
.certificates h2 {
    text-align: center;
    margin-bottom: 2rem;
}
.certificates{
    display: flex;
   
    flex-direction: column;
    padding: 2rem 8rem;

}

.testimonies{
    background: #F0F2FB;
    padding: 8rem;
}
.testimonies h1{
    font-size: 56px;
    max-width: 55%;
   
    font-weight: bold;
    color: #4F67FF;
}

.testimony-cards{
    display: flex;
    font-family: "Neuton";
    gap: 2rem;
}

.card{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.card .card-body{
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #fff;
    padding: 1rem;
    border-radius: 5px;
}
.card .card-body h2{
    font-size: 24x;
}
.card .card-body .rating svg{
    fill: #ffb710;

}
.card .icon{
    margin: 0px 0px -24px 0px;
    z-index: 1;
}
.card .card-body p{
    color: #52566C;
    font-size: 16px;

}

.action {
    background: #00CDC4;
    padding: 0rem 8rem;
    display: flex;
    gap: 15rem;
}
.action .right h2{
    font-size: 56px;
    color: #4F67FF;
}
.action .right p {
    color: #fff;
}
.action .right button{
    background: #4F67FF;
    color: #fff;
    width: 100%;

}

@media only screen and (max-width: 600px) {
    .action {
        background: #00CDC4;
        padding: 0rem .5rem;
        flex-direction: column;
        gap: 1rem;
    }
    .action .right button{
        margin-bottom: 2.5rem;
    }
    .action .right p {
        display: none;
    }
    header{
        background-color: #4F67FF;
        padding: 2rem .5rem;
    }
    section{
        padding: 2rem .5rem;
        flex-direction: column;
        align-items: center;
        gap: 2rem;
    
    }
    .span-group{
        flex-direction: column;
    }
    .certificates{
        padding: 2rem .5rem;
    }
    section.reverse{
        flex-direction: column-reverse;
        align-items: center;
       
    }
    .testimonies{
        padding: 4rem 1rem;
    }
    .testimonies h1{
        max-width: 100%;
    }
    
    .hero{
        display: flex;
        flex-direction: column;
    }
    .hero-text{
        font-size: 70px;
    }

    .testimony-cards{
        flex-direction: column;
    }
  }`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAEA;IACI,sBAAsB;AAC1B;;AAEA;CACC,uBAAuB;CACvB,SAAS;;AAEV;AACA;IACI,yBAAyB;IACzB,kBAAkB;;AAEtB;;;AAGA;IACI,aAAa;IACb,yBAAyB;IACzB,yCAAyC;IACzC,eAAe;IACf,sBAAsB;IACtB,uBAAuB;IACvB,WAAW;IACX,aAAa;IACb,YAAY;IACZ,kBAAkB;IAClB,UAAU;AACd;;AAEA;IACI,aAAa;IACb,mBAAmB;AACvB;AACA;IACI,aAAa;IACb,sBAAsB;IACtB,oBAAoB;IACpB,OAAO;AACX;AACA;IACI,OAAO;IACP,gBAAgB;IAChB,aAAa;IACb,gBAAgB;IAChB,oBAAoB;IACpB,sBAAsB;IACtB,YAAY;AAChB;;AAEA;IACI,eAAe;IACf,WAAW;AACf;AACA;AACA,qBAAqB;AACrB,cAAc;AACd,eAAe;;AAEf;;;;AAIA,YAAY;;AAEZ;IACI,kBAAkB;IAClB,aAAa;IACb,mBAAmB;IACnB,mBAAmB;IACnB,8BAA8B;IAC9B,qBAAqB;;AAEzB;;AAEA;IACI,aAAa;IACb,sBAAsB;AAC1B;AACA;IACI,SAAS;IACT,eAAe;IACf,gBAAgB;IAChB,mBAAmB;IACnB,sBAAsB;AAC1B;AACA;IACI,cAAc;IACd,eAAe;IACf,gBAAgB;AACpB;AACA;IACI,WAAW;IACX,eAAe;IACf,aAAa;IACb,SAAS;IACT,mBAAmB;IACnB,mBAAmB;AACvB;AACA;IACI,aAAa;AACjB;AACA,GAAG;AACH;IACI,aAAa;IACb,8BAA8B;IAC9B,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,8BAA8B;AAClC;AACA;IACI,aAAa;IACb,mBAAmB;IACnB,UAAU;IACV,mBAAmB;AACvB;AACA;IACI,aAAa;AACjB;AACA;IACI,kBAAkB;IAClB,mBAAmB;AACvB;AACA;IACI,aAAa;;IAEb,sBAAsB;IACtB,kBAAkB;;AAEtB;;AAEA;IACI,mBAAmB;IACnB,aAAa;AACjB;AACA;IACI,eAAe;IACf,cAAc;;IAEd,iBAAiB;IACjB,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,qBAAqB;IACrB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,gBAAgB;IAChB,aAAa;IACb,kBAAkB;AACtB;AACA;IACI,cAAc;AAClB;AACA;IACI,aAAa;;AAEjB;AACA;IACI,yBAAyB;IACzB,UAAU;AACd;AACA;IACI,cAAc;IACd,eAAe;;AAEnB;;AAEA;IACI,mBAAmB;IACnB,kBAAkB;IAClB,aAAa;IACb,UAAU;AACd;AACA;IACI,eAAe;IACf,cAAc;AAClB;AACA;IACI,WAAW;AACf;AACA;IACI,mBAAmB;IACnB,WAAW;IACX,WAAW;;AAEf;;AAEA;IACI;QACI,mBAAmB;QACnB,mBAAmB;QACnB,sBAAsB;QACtB,SAAS;IACb;IACA;QACI,qBAAqB;IACzB;IACA;QACI,aAAa;IACjB;IACA;QACI,yBAAyB;QACzB,mBAAmB;IACvB;IACA;QACI,mBAAmB;QACnB,sBAAsB;QACtB,mBAAmB;QACnB,SAAS;;IAEb;IACA;QACI,sBAAsB;IAC1B;IACA;QACI,mBAAmB;IACvB;IACA;QACI,8BAA8B;QAC9B,mBAAmB;;IAEvB;IACA;Q